import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import styles from './Emotion.css';
import EmotionChip from '../containers/EmotionChip';
import Footer from '../components/Footer';


const geoLocation = (callback) => {
    fetch(`/data/helplines.json`) // Get the helplines in each country 
    .then((r) => r.json())
    .then((helplines) =>{
        fetch(`https://ipapi.co/json/`) // Get country by ip
        .then((r) => r.json())
        .then((ipdata) =>{
            var country = helplines.filter(i=>i.country == ipdata.country_name); 
            callback(country[0]||{});
        })
    })
}


const shareApi = () => {
    if(navigator.share){
        navigator.share({
            title: "It's Totally OK",
            url: document.location.href
          }).then(() => {})
          .catch(console.error);
    }
}

const Emotion = ({data}) => {
    let {emotion} = useParams();
    const [country, setCountry] = useState({})
    emotion = emotion.toLowerCase();
    
    const emotionObj = data.emotions.find(i=>i.title.toLowerCase()==emotion);
    
    
    useEffect(() => {      
        if(!emotionObj)  
            return;

        const backgroundNum = Math.floor(Math.random() * data.backgrounds.length);
        const backgroundObj = data.backgrounds[backgroundNum];

        document.body.className = `emotion ${backgroundObj["light-content"]?'light':'dark'}`;
        document.body.style.backgroundColor = backgroundObj.color;

        geoLocation(setCountry);

        return () => {
            document.body.className = ``;
            document.body.style.backgroundColor = null;
        };

    },[emotionObj]);

    return(
    <><div className="emotion-container">
        <div className="title">
            <h2>It's totally</h2><h1>OK</h1><h3>to feel {emotionObj.title.toLowerCase()} <i>{emotionObj.icon}</i></h3>
        </div>
        <div className="links">
            {!emotionObj.positive && country.phones && <EmotionChip label={"Call Local Helpline"} icon={"ðŸ“ž"} iconFirst={true} url={`tel:${country.phones[0]}`} />}
            {emotionObj.links.map((i,k)=>{ return i.title ? <EmotionChip label={i.title} icon={i.icon} iconFirst={true} url={i.url} key={k}/> : null})}            
            <EmotionChip cssClass={"share"} label={"Share"} icon={"â†—ï¸"} iconFirst={true} chipClick={shareApi}/>
        </div>
       
    </div>
    <Footer/></>);
}
export default Emotion;