import React, {useRef} from 'react';
import {Link} from 'react-router-dom';
import styles from './EmotionChip.css';

const EmotionChip = ({label, icon, iconFirst, url, cssClass = '', chipClick}) => {
    let chipObj = <span className={`e-chip ${cssClass}`} onClick={chipClick}> 
        {iconFirst&&<i>{icon}</i>}
        &nbsp;{label}&nbsp;
        {!iconFirst&&<i>{icon}</i>}
    </span>;

    if(url){
        if(url.indexOf('/')==0){
            return <Link to={url}>{chipObj}</Link>
        }else{
            return <a href={url} target={"_blank"}>{chipObj}</a>
        }
    }
    return(chipObj);
}
export default EmotionChip;